import { importTypes } from '@rancher/auto-import';
import { IPlugin, OnNavToPackage, TabLocation } from '@shell/core/types';
import { STORAGE_CLASS, PVC } from '@shell/config/types';
import { _DETAIL } from '@shell/config/query-params';
import extensionRouting from './extension-routing';

const onEnter: OnNavToPackage = async(store) => {
  // Do nothing
};

// Init the package
export default function(plugin: IPlugin) {
  // Auto-import model, detail, edit from the folders
  importTypes(plugin);

  // Provide extension metadata from package.json
  // it will grab information such as `name` and `description`
  plugin.metadata = require('./package.json');

  // Load a product
  plugin.addProduct(require('./product'));

  // Add Vue Routes
  plugin.addRoutes(extensionRouting);

  // Add hooks to Vue navigation world
  plugin.addNavHooks(onEnter);

  /**
   * Adding Tab for Storage Class.
   * TODO for future release: Currently this is not working so following bug has been filed:
   * https://github.com/rancher/dashboard/issues/11122
   *
   * This need to be displayed for view only. No edit. Commenting the below code as the feauture is not working.
   * When rancher starts supporting adding tab inside Storage Cluster, uncomment the below code.
   */

  // plugin.addTab(
  //   TabLocation.RESOURCE_DETAIL,
  //   {
  //     resource: [STORAGE_CLASS],
  //     mode:     [_DETAIL]

  //   },
  //   {
  //     name:       'portworx',
  //     label:      'Portworx',
  //     weight:     99,
  //     showHeader: true,
  //     tooltip:    'This is a tooltip for Portworx tab',
  //     component:  () => import('./components/StorageClass/portworx.vue')
  //   }
  // );

  /**
   * Adding Tab for PVC. This will be displayed only during Display mode.
   */
  plugin.addTab(
    TabLocation.RESOURCE_DETAIL,
    {
      resource: [PVC],
      mode:     [_DETAIL]
    },
    {
      name:       'portworx',
      label:      'Portworx',
      weight:     -5,
      showHeader: false,
      component:  () => import('./components/PVC/pvc-portworx.vue'),
    }
  );
}
