import axios from 'axios';
import { API_ENDPOINTS } from '../api-endpoint';

export interface IService {
  metadata: {
    name: string;
    namespace: string;
  };
}

/**
 * This is a common utility service that provides helper functions for code reusability.
 */
export default {
  methods: {
    /**
     * To determine whether Portworx is installed or not, we need to get all services of the cluster.
     * If "portworx-api" service is found, then Portworx is installed.
     * Use the corresponding namespace to make further API calls.
     */
    async getPortworxNamespace(clusterId: string): Promise<[string, string]> {
      // Variables to return.
      let pxInstalledNS = '';
      let pageError = 'none';

      try {
        const servicesRes = await axios.get(API_ENDPOINTS.k8sAllServices(clusterId));
        const allServicesOfCluster = servicesRes.data.items;
        const portworxService: IService = allServicesOfCluster.find((service: IService) => service.metadata.name === 'portworx-api');

        if (!portworxService) {
          // If Portworx is not installed, then UI should show a banner and stop further API calls.
          pageError = 'px-not-installed';
        } else {
          pxInstalledNS = portworxService.metadata.namespace;
        }

        return [
          pxInstalledNS,
          pageError
        ];
      } catch (err) {
        pageError = 'failed-fetching-services';

        return [
          pxInstalledNS,
          pageError
        ];
      }
    },
    /**
     * Get the storage cluster details.
     * If the cluster is PX Secure cluster, then we have to set additional authorization headers.
     */
    async getStorageClusterDetails(clusterId: string, pxInstalledNS: string) {
      let cluster;
      let pxSecureAuthToken = '';
      let pageError = 'none';

      try {
        const storageClusterRes = await axios.get(
          API_ENDPOINTS.k8sClusterApi(clusterId, pxInstalledNS));

        cluster = storageClusterRes.data?.items[0] || {};
        if (cluster?.spec?.security?.enabled) {
          // PX-Secure cluster. In this case, we need to add additional authorization header to the API calls.

          // Fetch the auth-token.
          const adminTokenApiRes =
            await axios.get(API_ENDPOINTS.k8sPxAdminToken(clusterId, cluster.metadata?.namespace));
          const authToken = (adminTokenApiRes?.data?.data || {})['auth-token'];

          if (authToken) {
            pxSecureAuthToken = window.atob(authToken);
          }
        }

        return [
          cluster,
          pxSecureAuthToken,
          pageError
        ];
      } catch (error) {
        pageError = 'failed-fetching-cluster';

        return [
          cluster,
          pxSecureAuthToken,
          pageError
        ];
      }
    },
  }
};
