<template>
  <div>
    <header>
      <div class="title">
        <h1 style="display:flex; align-items: center;">
          <img :src="pxLogo" alt="logo" style="height: 30px; margin-right: 5px;">
          <span>{{ pageTitle }}</span>
        </h1>
      </div>
    </header>
    <div
      v-if="isInitDataFetching"
      style="display: flex; justify-content: center; align-items: center; height: 60vh;"
    >
      Loading...
    </div>
    <div v-else-if="!isInitDataFetching && pageError !== 'none'" id="banner-px-not-installed" style="margin-top: 1rem;">
      <Banner
        v-if="pageError === 'px-not-installed'"
        class="type-banner mb-20 mt-0"
        color="warning"
      >
        Portworx is not installed on this cluster. Click
        <a style="cursor: pointer;" :href="EXTERNAL_LINKS.INSTALL_PX" target="_blank">here</a>
        to learn how to install Portworx.
      </Banner>
      <Banner
        v-else-if="pageError === 'px-secure-cluster'"
        class="type-banner mb-20 mt-0"
        color="warning"
      >
        Portworx extension is currently not supported for PX-Security enabled clusters.
      </Banner>
      <Banner
        v-else-if="pageError === 'data-fetch-error'"
        class="type-banner mb-20 mt-0"
        color="warning"
      >
        Failed to load details. Please try again later.
      </Banner>
      <Banner
        v-else-if="pageError === 'failed-fetching-services'"
        class="type-banner mb-20 mt-0"
        color="error"
      >
        Unable to fetch services for the cluster. Please try again later.
      </Banner>
      <Banner
        v-else-if="pageError === 'failed-fetching-cluster'"
        class="type-banner mb-20 mt-0"
        color="error"
      >
        Unable to retrieve storage cluster details. Please try again later.
      </Banner>
    </div>
    <div v-else>
      <div id="cluster-details-and-activity" class="sections first-section-col">
        <div style="display: flex; height:550px;">
          <div class="simple-box container">
            <h3 style="margin-bottom: 2rem;">
              <span>
                Cluster Details
              </span>
            </h3>
            <div v-if="!isInitDataFetching">
              <ConsumptionGauge
                :used="pxNodesInfo.usedSize"
                :capacity="pxNodesInfo.totalSize"
                units="GiB"
              />
            </div>
            <div style="display: flex; margin-top: 1.5rem;">
              <div id="cluster-details-left" style="display: flex; flex-direction: column; width: 50%; padding-right: 15px;">
                <div style="margin-bottom: 1.5rem;">
                  <h4>Name</h4>
                  <h5>{{ (cluster?.metadata || {}).name || `-` }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Version</h4>
                  <h5>{{ (cluster?.status || {}).version || `-` }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Status</h4>
                  <h5>{{ (cluster?.status || {}).phase || `-` }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Telemetry Status</h4>
                  <h5>
                    {{ (((cluster?.spec || {}).monitoring || {}).telemetry || {})
                      .enabled
                      ? 'Enabled'
                      : 'Disabled' }}
                  </h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>License</h4>
                  <h5>
                    {{ license?.status?.sku }} {{ license?.status?.expires_on ? '(Expires on: ' + new Date(license?.status?.expires_on).toLocaleDateString() + ')' : '' }}
                  </h5>
                </div>
              </div>
              <div id="cluster-details-right" style="display: flex; flex-direction: column; width: 50%; padding-left: 15px;">
                <div style="margin-bottom: 1.5rem;">
                  <h4>UUID</h4>
                  <h5>{{ (cluster?.status || {}).clusterUid || `-` }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Operator Version</h4>
                  <h5>{{ pxOperatorVersion || '-' }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Monitoring Status</h4>
                  <h5>{{ !!cluster?.spec?.monitoring?.prometheus?.exportMetrics ? 'Enabled' : 'Disabled' }}</h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Stork Version</h4>
                  <h5>
                    {{ ((
                      ((cluster?.status || {}).desiredImages || {}).stork || ''
                    ).split(':') || [])[1] }}
                  </h5>
                </div>
                <div style="margin-bottom: 1.5rem;">
                  <h4>Number of Nodes</h4>
                  <h5>
                    <div style="margin-bottom: 5px;">
                      {{ pxNodesInfo.storageNode || '0' }} Storage node(s)
                    </div>
                    <div>{{ pxNodesInfo.storageLessNode || '0' }} Storageless node(s)</div>
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style="display: flex; height:550px;">
          <div class="simple-box container">
            <h3 style="margin-bottom: 2rem;">
              <span>
                Activity
              </span>
            </h3>
            <div style="height: calc(100% - 50px); overflow-y: auto; ">
              <div v-if="activities?.length > 0">
                <div v-for="(activity, index) in activities" :key="index" style="margin-bottom: 1rem; " class="activity-banner" :class="[activity.type === 'Warning' ? 'warning-banner' : 'info-banner']">
                  <div>
                    <span> {{ activity.deprecatedFirstTimestamp ? '[' + new Date(activity.deprecatedFirstTimestamp).toLocaleString() + '] ': '' }}{{ activity.note }}</span>
                    <br>
                    <br>
                    <span>{{ activity.regarding.kind }} : {{ activity.regarding.name }}</span>
                    <br>
                    <span> Namespace : {{ activity.regarding.namespace }}</span>
                  </div>
                </div>
              </div>
              <div v-else>
                <Banner
                  class="type-banner mb-20 mt-0"
                  color="info"
                  :label="isInitDataFetching ? 'Loading activities...' : 'No activities found.'"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id="resources-section" class="sections section-full">
        <div>
          <div class="simple-box container">
            <h3>
              <span>
                Resources Section
              </span>
            </h3>
            <div>
              <Tabbed
                ref="tabs"
                :side-tabs="false"
                class="step__values__content"
                @changed="tabChanged($event)"
              >
                <Tab name="Volumes" label="Volumes" :weight="2">
                  <div name="Volumes" style="max-height:410px; overflow-y: auto; ">
                    <ResourceTable
                      v-if="pxVolumes?.length > 0"
                      data-testid="volumes-sortable-table"
                      :rows="pxVolumes"
                      :headers="VOLUMES_HEADERS"
                      :table-actions="false"
                      :row-actions="false"
                      :search="false"
                      key-field="volume.id"
                    >
                      <template #col:name="{row}">
                        <td>
                          <span>{{ row?.name || '-' }}</span>
                        </td>
                      </template>

                      <template #col:namespace="{row}">
                        <td>
                          <span>{{ (row.labels || {}).namespace || '-' }}</span>
                        </td>
                      </template>

                      <template #col:pvc="{row}">
                        <td>
                          <span v-if="!(row.labels || {}).pvc">-</span>
                          <span v-else>
                            <router-link :to="getPvcUrl(row.labels)">
                              {{ row.labels.pvc }}
                            </router-link>
                          </span>
                        </td>
                      </template>

                      <template #col:status="{row}">
                        <td>
                          <span>{{ ((row.volume || {}).status.split('_') || [])[2] || '-' }}</span>
                        </td>
                      </template>

                      <template #col:attachedNode="{row}">
                        <td>
                          <span>{{ (row.volume || {}).attached_on || '-' }}</span>
                        </td>
                      </template>

                      <template #col:replica="{row}">
                        <td>
                          <span>{{ (row.labels || {}).repl || '-' }}</span>
                        </td>
                      </template>

                      <template #col:capacity="{row}">
                        <td>
                          <span>{{ `${convertToGi(((row.volume || {}).spec || {}).size)} GiB` || '-' }}</span>
                        </td>
                      </template>
                    </ResourceTable>
                    <div v-else>
                      <Banner
                        class="type-banner mb-20 mt-0"
                        color="info"
                        :label="isInitDataFetching ? 'Loading volumes...' : 'No volumes found.'"
                      />
                    </div>
                  </div>
                </Tab>
                <Tab name="Drives" :weight="1">
                  <div name="Drives" style="max-height:410px; overflow-y: auto; ">
                    <ResourceTable
                      v-if="pxNodesInfo?.allDrives?.length > 0"
                      data-testid="drives-sortable-table"
                      :rows="pxNodesInfo.allDrives"
                      :headers="DRIVES_HEADERS"
                      :table-actions="false"
                      :row-actions="false"
                      :search="false"
                    >
                      <template #col:name="{row}">
                        <td>
                          <span>{{ row.name || '-' }}</span>
                        </td>
                      </template>

                      <template #col:node="{row}">
                        <td>
                          <span>{{ row.node || '-' }}</span>
                        </td>
                      </template>

                      <template #col:capacity="{row}">
                        <td>
                          <span>{{ `${convertToGi(row?.used)} GiB/ ${convertToGi(row?.size)} GiB` }}</span>
                        </td>
                      </template>
                    </ResourceTable>
                    <div v-else>
                      <Banner
                        class="type-banner mb-20 mt-0"
                        color="info"
                        :label="isInitDataFetching ? 'Loading drives...' : 'No drives found.'"
                      />
                    </div>
                  </div>
                </Tab>
                <Tab name="Pools" :weight="0">
                  <div name="Pools" style="max-height:410px; overflow-y: auto; ">
                    <ResourceTable
                      v-if="pxNodesInfo?.allPools?.length > 0"
                      data-testid="pools-sortable-table"
                      :rows="pxNodesInfo.allPools"
                      :headers="POOLS_HEADERS"
                      :table-actions="false"
                      :row-actions="false"
                      :search="false"
                    >
                      <template #col:uuid="{row}">
                        <td>
                          <span>{{ row.uuid || '-' }}</span>
                        </td>
                      </template>

                      <template #col:ioPriority="{row}">
                        <td>
                          <span>{{ row.Cos || '-' }}</span>
                        </td>
                      </template>

                      <template #col:medium="{row}">
                        <td>
                          <span>{{ row.Medium ? row.Medium : (row.labels || {}).medium?.split('_')[2] }}</span>
                        </td>
                      </template>

                      <template #col:node="{row}">
                        <td>
                          <span>{{ row.node || '-' }}</span>
                        </td>
                      </template>

                      <template #col:capacity="{row}">
                        <td>
                          <span>{{ `${convertToGi(row?.Used)} GiB/ ${convertToGi(row?.TotalSize)} GiB` }}</span>
                        </td>
                      </template>
                    </ResourceTable>
                    <div v-else>
                      <Banner
                        class="type-banner mb-20 mt-0"
                        color="info"
                        :label="isInitDataFetching ? 'Loading pools...' : 'No pools found.'"
                      />
                    </div>
                  </div>
                </Tab>
              </Tabbed>
            </div>
          </div>
        </div>
      </div>
      <div id="nodes-section" class="sections section-full">
        <div>
          <div class="simple-box container">
            <h3>
              <span>
                Node Summary
              </span>
            </h3>
            <div style="max-height:410px; overflow-y: auto; ">
              <ResourceTable
                v-if="pxNodesInfo?.allNodes?.length > 0"
                data-testid="nodes-sortable-table"
                :rows="pxNodesInfo.allNodes"
                :headers="NODES_HEADERS"
                :table-actions="false"
                :row-actions="false"
                :search="false"
                key-field="id"
              >
                <template #col:name="{row}">
                  <td>
                    <span>{{ row.hostname || '-' }}</span>
                  </td>
                </template>

                <template #col:ip="{row}">
                  <td>
                    <span>{{ row.data_ip || '-' }}</span>
                  </td>
                </template>

                <template #col:status="{row}">
                  <td>
                    <span>{{ (row?.status || '')?.toLowerCase().replace('status_', '') }}</span>
                  </td>
                </template>

                <template #col:pxVersion="{row}">
                  <td>
                    <span>{{ (row?.node_labels || {})['PX Version'] }}</span>
                  </td>
                </template>

                <template #col:usedCapacity="{row}">
                  <td>
                    <span>{{ getTotalSize(row) }}</span>
                  </td>
                </template>
              </ResourceTable>
              <div v-else>
                <Banner
                  class="type-banner mb-20 mt-0"
                  color="info"
                  :label="isInitDataFetching ? 'Loading nodes...' : 'No nodes found'"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Tabbed from '@shell/components/Tabbed';
import Tab from '@shell/components/Tabbed/Tab';
import ConsumptionGauge from '@shell/components/ConsumptionGauge';
import { Banner } from '@components/Banner';
import ResourceTable from '@shell/components/ResourceTable';
import utilService from '../../../../services/util-service';
import { EXTERNAL_LINKS } from '../../../../constants';
import { API_ENDPOINTS } from '../../../../api-endpoint';
import pxLogo from '../../../../assets/small.svg';
import { NODES_HEADERS, VOLUMES_HEADERS, DRIVES_HEADERS, POOLS_HEADERS } from './table-headers';

export default {
  name: 'PortworxClusters',

  components: {
    ResourceTable, Tabbed, Tab, Banner, ConsumptionGauge
  },
  data() {
    return {
      isInitDataFetching: true,
      pageError:          'none',
      pxInstalledNS:      '',
      pageTitle:          'Portworx',
      license:            {},
      cluster:            {},
      pxNodesInfo:        {},
      pxVolumes:          [],
      activities:         [],
      pxOperatorVersion:  '',
      pxSecureAuthToken:  '',
      NODES_HEADERS,
      VOLUMES_HEADERS,
      DRIVES_HEADERS,
      POOLS_HEADERS,
      EXTERNAL_LINKS,
      pxLogo
    };
  },
  methods: {
    // App init happens from this point. This is called from the "created" lifecycle hook.
    async fetchData() {
      try {
        this.pageError = 'none';
        // Set external links
        this.EXTERNAL_LINKS = EXTERNAL_LINKS;
        // Get the cluster name from the route.
        const CLUSTER_NAME = this.$route.params.cluster;

        this.isInitDataFetching = true;

        // Fetch the namespace on which Portworx is installed.
        [this.pxInstalledNS, this.pageError] = await utilService.methods.getPortworxNamespace(CLUSTER_NAME);
        if (this.pageError !== 'none') {
          // If Portworx is not installed or init data fetch failed, then do not proceed further.
          this.isInitDataFetching = false;

          return;
        }

        // Get the storage Cluster details. If the cluster is PX Secure cluster, then we will get pxSecureAuthToken
        // which will be used for further API calls.
        [this.cluster, this.pxSecureAuthToken, this.pageError] =
          await utilService.methods.getStorageClusterDetails(CLUSTER_NAME, this.pxInstalledNS);
        if (this.pageError !== 'none') {
          // If any error occurred while fetching the storage cluster details, then do not proceed further.
          this.isInitDataFetching = false;

          return;
        }

        const pxApiConfig = {};

        // For PX Secure cluster, we will have pxSecureAuthToken token.
        // In this case, for API calls made to portworx-api, we need to set the Authorization header.
        if (this.pxSecureAuthToken) {
          pxApiConfig.headers = { Authorization: `Bearer ${ this.pxSecureAuthToken }` };
        }

        // Make API calls to fetch required data for the Portworx cluster.
        const [
          licenseResponse,
          pxOperatorResponse,
          pxNodeResponse,
          pxVolumeResponse,
        ] = await Promise.all([
          axios.get(API_ENDPOINTS.pxLicense(CLUSTER_NAME, this.pxInstalledNS), pxApiConfig).then(res => res).catch((error) => {
            if (error.response.status === 403) {
              this.pageError = 'px-secure-cluster';
            }

            return {};
          }),
          axios.get(API_ENDPOINTS.k8sOpetators(CLUSTER_NAME), { headers: { Accept: 'application/json' } }),
          axios.get(API_ENDPOINTS.pxNodeApi(CLUSTER_NAME, this.pxInstalledNS), pxApiConfig).then(res => res).catch((error) => {
            if (error.response.status === 403) {
              this.pageError = 'px-secure-cluster';
            }

            return {};
          }),
          axios.post(API_ENDPOINTS.pxFilterVolumes(CLUSTER_NAME, this.pxInstalledNS), {}, pxApiConfig).then(res => res).catch((error) => {
            if (error.response.status === 403) {
              this.pageError = 'px-secure-cluster';
            }

            return {};
          }),
        ]);

        if (this.pageError !== 'none') {
          // If any error occurred while fetching the storage cluster details, then do not proceed further.
          this.isInitDataFetching = false;

          return;
        }

        // Handle the results
        this.license = licenseResponse.data;

        const clusterEvents = await axios.get(API_ENDPOINTS.k8sClusterEvents(CLUSTER_NAME, this.cluster?.metadata?.namespace));

        this.activities = clusterEvents.data?.items || [];
        this.activities = this.activities.reverse();

        // Find the correct portworx-operator.
        const pxOperator =
          ((pxOperatorResponse || {}).data?.items || []).find((operator = {}) => (operator.metadata || {}).name.includes('portworx-operator'),
          ) || {};

        // Get the version of the portworx-operator to be displayed in UI.
        this.pxOperatorVersion = this.getPortworxOperatorVersion(pxOperator);

        this.pxNodesInfo = this.computeNodeDetails(pxNodeResponse.data);

        this.pxVolumes = pxVolumeResponse.data?.volumes || [];

        this.isInitDataFetching = false;
      } catch (error) {
        this.pageError = 'data-fetch-error';
        this.isInitDataFetching = false;
      }
    },
    /**
     * Fetch the portworx-operator version to be displayed in the summary section.
     * @param portworxOperator
     */
    getPortworxOperatorVersion(portworxOperator) {
      const containers = portworxOperator?.spec?.containers || [];
      let operatorVersion = '-';

      for (let index = 0; index < containers.length; index++) {
        const container = containers[index];

        if (container?.name === 'portworx-operator') {
          operatorVersion = container?.image.split(':')[1];
          break;
        }
      }

      return operatorVersion;
    },
    /**
     * This method computes the following details:
     *  - Used and total size of the storage cluster for the bar chart.
     *  - Storage node count and storage-less node count for the summary section.
     *  - Extracts the nodes, pools and drives information to be displayed in the table.
     * @param nodeSummary
     */
    computeNodeDetails(nodeSummary) {
      let storageNode = 0;
      let totalSize = 0;
      let usedSize = 0;
      let storageLessNode = 0;
      let usedPercentage = 0;
      const allNodes = nodeSummary.nodes || [];
      const allPools = [];
      const allDrives = [];

      for (let index = 0; index < (nodeSummary.nodes || []).length; index++) {
        const node = nodeSummary.nodes[index];

        // Compute total and used size
        if (node?.pools?.length) {
          for (let poolIndex = 0; poolIndex < node.pools.length; poolIndex++) {
            const pool = node.pools[poolIndex];

            usedSize += Number(pool?.Used.replace(/[^0-9]/g, ''));
            totalSize += Number(pool?.TotalSize.replace(/[^0-9]/g, ''));

            allPools.push({
              ...(node.pools[poolIndex] || {}),
              node: node?.hostname,
            });
          }
          storageNode++;
        } else {
          storageLessNode++;
        }

        // Compute all drives
        for (const key in node.disks) {
          if (Object.hasOwnProperty.call(node.disks, key)) {
            allDrives.push({
              ...(node.disks[key] || {}),
              name: key,
              node: node?.hostname,
            });
          }
        }
      }

      usedPercentage = totalSize ? Number(((usedSize / totalSize) * 100).toFixed(2)) : 0;
      // eslint-disable-next-line prefer-template
      usedPercentage = usedPercentage + '';

      return {
        storageNode,
        storageLessNode,
        usedPercentage,
        allNodes,
        allPools,
        allDrives,
        totalSize: Number(this.convertToGi(totalSize)),
        usedSize:  Number(this.convertToGi(usedSize)),
      };
    },
    convertToGi(data) {
      if (!data) {
        return '0';
      }
      if (typeof data === 'string') {
        if (data.includes('M')) {
          return data;
        } else if (data.includes('K')) {
          return data;
        } else if (data.includes('G')) {
          return data;
        }
        data = Number(data);
      }

      return (data / Math.pow(2, 10 * 3)).toFixed();
    },
    getTotalSize(node) {
      if (node && node?.pools?.length) {
        let usedSize = 0;
        let totalSize = 0;

        for (let poolIndex = 0; poolIndex < node.pools.length; poolIndex++) {
          const pool = node.pools[poolIndex];

          usedSize += Number(pool?.Used.replace(/[^0-9]/g, ''));
          totalSize += Number(pool?.TotalSize.replace(/[^0-9]/g, ''));
        }

        return `${ this.convertToGi(usedSize) } GiB/ ${ this.convertToGi(totalSize) } Gib`;
      } else {
        return '0GiB / 0GiB';
      }
    },
    getPvcUrl(labels) {
      return `/c/${ this.$route.params.cluster }/explorer/persistentvolumeclaim/${ labels.namespace }/${ labels.pvc }#portworx`;
    },
    tabChanged(tab) {
      // Add code to handle tab change if required.
    }
  },
  created() {
    // Entry point for the application.
    this.fetchData();
  },
};

</script>

