export const API_ENDPOINTS = {
  // k8s APIs
  k8sClusterApi:    (clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/apis/core.libopenstorage.org/v1/namespaces/${ ns }/storageclusters`,
  k8sNodeApi:       (clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/apis/core.libopenstorage.org/v1/namespaces/${ ns }/storagenodes`,
  k8sAllServices:   (clusterId: string) => `/k8s/clusters/${ clusterId }/api/v1/services`,
  k8sClusterEvents: (clusterId: string, clusterNs = 'portworx' ) => `/k8s/clusters/${ clusterId }/apis/events.k8s.io/v1/namespaces/${ clusterNs }/events`,
  k8sOpetators:     (clusterId: string) => `/k8s/clusters/${ clusterId }/api/v1/pods?labelSelector=name=portworx-operator`,
  k8sPVs:           (clusterId: string) => `/k8s/clusters/${ clusterId }/v1/persistentvolumes`,
  k8sPxAdminToken:  (clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/secrets/px-admin-token`,
  // PX APIs
  pxVolumesInspect: ( clusterId: string, volId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/services/portworx-api:9021/proxy/v1/volumes/inspect/${ volId }`,
  pxLicense:        ( clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/services/portworx-api:9021/proxy/v1/license/status`,
  pxFilterVolumes:  ( clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/services/portworx-api:9021/proxy/v1/volumes/inspectwithfilters`,
  pxNodeInspectApi: ( clusterId: string, nodeId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/services/portworx-api:9021/proxy/v1/nodes/inspect/${ nodeId }`,
  pxNodeApi:        ( clusterId: string, ns = 'portworx' ) => `/k8s/clusters/${ clusterId }/api/v1/namespaces/${ ns }/services/portworx-api:9021/proxy/v1/nodes/filters`,
};
