import { IPlugin } from '@shell/core/types';

export const YOUR_PRODUCT_NAME = 'portworx';
export function init($plugin: IPlugin, store: any) {
  const { product, basicType, virtualType } = $plugin.DSL(store, YOUR_PRODUCT_NAME);

  product({
    inStore:             'cluster',
    // inExplorer:          true,
    icon:                'gear',
    // removeable:          false,
    showNamespaceFilter: true
  });

  virtualType({
    labelKey:       'Dashboard',
    // icon:        'gear',
    name:        'Dashboard',
    namespaced:  false,
    // weight:      99,
    route:       {
      name:   `c-cluster-${ YOUR_PRODUCT_NAME }`,
      params: { product: YOUR_PRODUCT_NAME },
      meta:   { pkg: YOUR_PRODUCT_NAME, product: YOUR_PRODUCT_NAME }
    },
    // overview: true
  });

  // Below line adds an existing resource to the navigation menu.
  // In main-component.vue layout: 'plain' is used to hide the sidebar. Comment it to see the below code in action.
  basicType(['Dashboard']);
}
